<template>
  <div class="app">
    <div class="products-container">
      <h1>Products</h1>
      <div class="products">
        <div v-for="product in products" :key="product.id" class="product">
          <h2>{{ product.name }}</h2>
          <p>{{ product.description }}</p>
          <p>Price: ${{ product.price }}</p>
          <button @click="addToCart(product)"><i class="fas fa-cart-plus"></i></button>
        </div>
      </div>
    </div>
    <div class="cart-container">
      <h1>Cart</h1>
      <div v-if="cartItems.length === 0">Your cart is empty.</div>
      <div v-else>
        <div v-for="(item, index) in cartItems" :key="index" class="cart-item">
          <div class="item-info">
            <h2>{{ item.name }}</h2>
            <p>Price: ${{ item.price }}</p>
          </div>
          <button @click="removeFromCart(index)"><i class="fas fa-trash"></i></button>
        </div>
        <p class="total">Total: ${{ total }}</p>
        <button @click="checkout" :disabled="cartItems.length === 0">Checkout</button>
      </div>
    </div>
  </div>
</template>

<script>
import axios from 'axios';
import { library } from '@fortawesome/fontawesome-svg-core';
// import { FontAwesomeIcon } from '@fortawesome/vue-fontawesome';
import { faCartPlus, faTrash } from '@fortawesome/free-solid-svg-icons';

library.add(faCartPlus, faTrash);

export default {
  name: 'App',
  components: {
    // FontAwesomeIcon,
  },
  data() {
    return {
      products: [],
      cartItems: [],
      total: 0,
    };
  },
  mounted() {
    this.fetchProducts();
  },
  methods: {
    fetchProducts() {
      axios.get('http://localhost:8000/api/products')
        .then(response => {
          this.products = response.data;
        })
        .catch(error => {
          console.error(error);
        });
    },
    addToCart(product) {
      this.cartItems.push(product);
      this.total += product.price;
    },
    removeFromCart(index) {
      const item = this.cartItems[index];
      this.total -= item.price;
      this.cartItems.splice(index, 1);
    },
    checkout() {
      axios.post('http://localhost:8000/api/orders', { items: this.cartItems, total: this.total })
        .then(response => {
          console.log('Order placed:', response.data);
          this.cartItems = [];
          this.total = 0;
        })
        .catch(error => {
          console.error(error);
        });
    },
  },
};
</script>

